.Header {

    display: flex;
    background-color: white;
    align-items: center;
    justify-content: space-around;
    font-family: "Fira Mono";
    font-size: 2.5vh;
    height: 10vh;

    a{
        text-decoration: none;
    }

    .MenuItem, .Logo, .Profile{
        color: black;
        margin: 0 10px;
    }

    .MenuItem:hover {
        scale: 1.1;
        transition: 100ms;
    }

    .Logo {
        font-family: "Libre Barcode 39 Text";
        font-size: 10vh;
    }

    .Search {


        .SearchInput{
            display: flex;
            align-items: center;
        }

        input {
            font-size: 3vh;
            height: 5vh;
            font-family: "Fira Mono";
            width: 40vh;

        }

        input::-webkit-search-cancel-button{
            -webkit-appearance: none;
            height: 20px;
            width: 20px;
            margin-right: 5px;
            font-family: "Material Icons";
            content: url("../../../public/close_icon.svg");
            fill: yellow;
            transition: scale 500ms;
        }

        input::-webkit-search-cancel-button:hover{
            scale: 1.2;
        }

        i {
            margin-top: 7px;
            font-size: 4vh;
        }

    }

    .Profile {

        display: flex;
        align-items: center;
        height: 100%;

        img,
        object {
            width: 7vh;
            height: 7vh;
            object-fit: fill;
            border-radius: 100%;
            margin-left: 10px;
        }

    }

    .Auth {

        .SignIn {
            padding: 8px;
        }

        .SignIn:hover {
            scale: 1.1;
            transition: 100ms;
            background-color: black;
            padding: 8px;
            border-radius: 10px;
            color: white;
        }

        .SignUp {
            border: 3px solid black;
            padding: 5px;
            border-radius: 10px;
        }

        .SignUp:hover {
            background-color: black;
            color: white;

        }

    }
}
